html,
body {
    height: 100%;
}

body {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px auto minmax(120px, 150px);
    grid-template-areas:"header"
                        "main"
                        "aside"
                        "footer";
    grid-gap: 1rem;
    
}

header {
    grid-area: header;
}

main {
    grid-area: main;
    position: relative;
}
aside {
    grid-area: aside;
}

footer {
    grid-area: footer;
    font-size: 75%;
}

button{
    // Remove unpleasant blue border on click in chrome
    &:focus{
        outline:none;
    }
}
.alert-danger{
    color: inherit !important;
}
@include media-breakpoint-up(lg) {
    body {
   
        grid-template-rows: 80px auto 50px;
    }
}